import React from "react";

export default function Projetos() {
  const cursos = [
    {
      ano: 2017,
      titulo: "Análise Estruturada de Sistemas. (Carga horária: 35h).",
      instituicao: "Fundação Bradesco, BRADESCO, Brasil.",
    },
    {
      ano: 2017,
      titulo: "Segurança da Informação. (Carga horária: 23h).",
      instituicao: "Fundação Bradesco, BRADESCO, Brasil.",
    },
    {
      ano: 2017,
      titulo: "PHP Orientado a Objetos. (Carga horária: 40h).",
      instituicao: "CursoEmVideo.com, CEV, Brasil.",
    },
    {
      ano: 2017,
      titulo: "Algoritmo. (Carga horária: 40h).",
      instituicao: "CursoEmVideo.com, CEV, Brasil.",
    },
    {
      ano: 2017,
      titulo: "C++ - Desenvolvimento Orientado a Objeto. (Carga horária: 35h).",
      instituicao: "Fundação Bradesco, BRADESCO, Brasil.",
    },
    {
      ano: 2017,
      titulo: "Inovando com CSS. (Carga horária: 26h).",
      instituicao: "Fundação Bradesco, BRADESCO, Brasil.",
    },
    {
      ano: 2016,
      titulo: "SQL Completo. (Carga horária: 20h).",
      instituicao: "Softblue.com.br, SB, Brasil.",
    },
    {
      ano: 2016,
      titulo: "Lógica de Programação. (Carga horária: 20h).",
      instituicao: "Softblue.com.br, SB, Brasil.",
    },
    {
      ano: 2017,
      titulo: "PHP. (Carga horária: 40h)..",
      instituicao: "CursoEmVideo.com, CEV, Brasil.",
    },
    {
      ano: 2015,
      titulo: "Segurança da Informação I. (Carga horária: 6h).",
      instituicao: "Intel, INTEL, Brasil.",
    },
  ];

  const apresentacoes = [
    {
    titulo: 'Robot Tracking Method with simple labels in robot.',
    ano: 2018,
    evento: 'Edingurgh Mini Maker Faire.',
    local: 'Edinburgh, Escócia.',
    },{
    titulo: 'Tecnica de rastreamento de etiquetas simples no Futebol de Robos.',
    ano: 2018,
    evento: 'Mostra Nacional de Robótica.',
    local: 'Juazeiro do Norte - CE, Brasil.'
    },{
    titulo: 'Serviço WEB para Registro de Artsões, Divulgação de suas Obras e Eventos culturais da Região.',
    ano: 2016,
    evento: 'MOCICA.',
    local: 'Juazeiro do Norte - CE, Brasil.'
    },{
    titulo: 'TÉCNICA DE RASTREAMENTO DE ROBÔS COM ETIQUETAS SIMPLES NO FUTEBOL DE ROBÔS.',
    ano: 2017,
    evento: 'MOCICA.',
    local: 'Juazeiro do Norte - CE. Brasil.'
    },{
    titulo: 'DescubraMatematica: A game for learning trigonometry and flat geometry in high school.',
    ano: 2019,
    evento: 'SBGames.',
    local: 'Rio de Janeiro - RJ. Brasil',
    }
  ]

  function renderCursos() {
    return cursos.map((curso, index) => {
      return (
        <div className="d-flex flex-column flex-md-row justify-content-between mb-8">
          <div className="flex-grow-1">
            <h3 className="mb-0">{curso.titulo}</h3>
            <div className="subheading mb-3">{curso.instituicao}</div>
          </div>
          <div className="flex-shrink-0">
            <span className="text-primary">{curso.ano}</span>
          </div>
        </div>
      );
    });
  }

  function renderApresentacoes() {
    return apresentacoes.map((apresentacao, index) => {
      return (
        <div className="d-flex flex-column flex-md-row justify-content-between mb-8">
          <div className="flex-grow-1">
            <h3 className="mb-0">{apresentacao.titulo}</h3>
            <div className="subheading mb-3">{apresentacao.evento} - {apresentacao.local}</div>
          </div>
          <div className="flex-shrink-0">
      <span className="text-primary">{apresentacao.ano}</span>
          </div>
        </div>
      );
    });
  }

  return (
    <>
      <section className="resume-section" id="projetos">
        <div className="resume-section-content">
          <h2 className="mb-5">Projetos e Conquistas</h2>
          <ul className="fa-ul mb-0">
            <li>
              <div className="tabs">
                <div className="tab">
                  <input type="checkbox" id="chck1" />
                  <label className="tab-label" htmlFor="chck1">
                    Cursos
                  </label>
                  <div className="tab-content">
                    {renderCursos()}
                  </div>
                </div>
                <div className="tab">
                  <input type="checkbox" id="chck2" />
                  <label className="tab-label" htmlFor="chck2">
                    Apresentações
                  </label>
                  <div className="tab-content">
                    {renderApresentacoes()}
                  </div>
                </div>
              </div>
            </li>
          </ul>
        </div>
      </section>
    </>
  );
}
